.menuContainer {
  cursor: pointer;

  min-width: 30px;
  height: 30px;

  background: url('../../../assets/img/icons/dotsMenu3.svg') 50% no-repeat;
  border-radius: 30px;
}

.menu {
  position: relative;
  top: 2px;
}

.openMenuLeft {
  position: relative;
  z-index: 3;

  &::before {
    content: '';

    position: absolute;
    z-index: 2;
    right: 30px;

    width: 95px;
    height: 30px;

    background: white;
    border-radius: 5px;
    box-shadow: 0px 5px 6px rgb(0 0 0 / 20%);

    animation: animateWidth 0.3s ease forwards;
  }

  &::after {
    content: '';

    position: absolute;
    z-index: 2;
    top: 10px;
    left: -7px;
    transform: rotate(-135deg) translate(-0.7px, 0.7px);

    width: 10px;
    height: 10px;

    background: white;
    clip-path: polygon(0% 0%, 100% 100%, 0% 100%);
    border-radius: 0 0 0 0.25em;
  }
}

.menuItem {
  position: absolute;
  z-index: -1;
  top: 0;

  width: 24px;
  height: 24px;

  opacity: 0;
  border-radius: 30px;

  animation: openToLeft 0.4s ease forwards;

  &:nth-child(1) {
    right: 65px;

    background: url('../../../assets/img/icons/editPack.svg') 50% no-repeat;

    animation-delay: 0.1s;
  }

  &:nth-child(2) {
    right: 35px;

    background: url('../../../assets/img/icons/deletePack.svg') 50% no-repeat;

    animation-delay: 0.2s;
  }
}

.portalBgColor {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  background: rgb(0 0 0 / 53%);
}

.modalsContainer {
  position: absolute;
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  align-self: center;

  width: 100%;
  max-width: 400px;
  padding: 30px;

  text-align: center;

  background: white;
  border-radius: 20px;
}
@keyframes animateWidth {
  0% {
    max-width: 0vw;
  }

  100% {
    max-width: 64px;
  }
}
@keyframes openToLeft {
  0% {
    z-index: 2;
    transform: translate(15px, 0px);

    opacity: 0;
  }

  100% {
    z-index: 2;
    transform: translate(0, 0);

    opacity: 1;
  }
}
