.tableCards {
  overflow: hidden;
  table-layout: fixed;
  border-collapse: collapse;

  width: 100%;

  border-radius: 5px;
}

.cards {
  &__emptyBlock {
    margin: 86px auto 0;

    text-align: center;
  }

  &__emptyTitle {
    margin: 0px 0px 32px 0px;

    font-weight: 500;
    line-height: 18px;
    color: grey;
  }
}
