.CardContainer {
  width: 100%;
  height: 100%;
}

.CardTypeContainer {
  width: 100%;

  &__title {
    display: block;

    margin-bottom: 10px;
  }
}

.Type {
  margin-bottom: 10px;
}

.AddTextCard,
.FormControl {
  width: 100%;
}

.Error {
  width: 100%;
  height: 10px;
  margin-bottom: 5px;

  visibility: hidden;

  &__active {
    color: red;

    visibility: visible;
  }
}

.Submit {
  display: flex;
  justify-content: space-between;

  width: 100%;
}
